.packages {
	position: relative;
	z-index: 5;
	
	text-align: center;
	@media @mobile {
       margin-top: 0px;
    }
    @media @tablet {
		margin-top: -100px;
    }
	.container {
	    @media @mobile {
	       
	    }
	    @media @tablet {
			margin-top: -240px;
	    }
	    @media @desktop {	
			margin-top: -270px;
	    }
	    @media @desktop-xl {
			width: 100%;
			margin-top: -100px;
		    
	    }
	}
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 352px;
		height: auto;
		background: #fff;
		-webkit-box-shadow: -16px 27px 51px rgba(95,82,122,.25);
		-moz-box-shadow: -16px 27px 51px rgba(95,82,122,.25);
		box-shadow: -16px 27px 51px rgba(95,82,122,.25);
		border-radius: 15px 15px 15px 15px;
		-moz-border-radius: 15px 15px 15px 15px;
		-webkit-border-radius: 15px 15px 15px 15px;
		padding-bottom: 25px;

		margin: 0 25px;
		transition: all 0.5s ease;
    	-webkit-transition: all 0.5s ease;
        -ms-transition: all 0.5s ease;

		@media @mobile {
			margin-bottom: 40px;
			width: 475px;
		}
		@media @tablet {
			width: 309px;
		}
		@media @desktop {
			width: 352px;
			margin-bottom: 0;
		}
		@media @desktop-xl {
			margin: 0 15px;
		}
		@media @desktop-xl-2 {
			 margin: 0 25px;
		}
		@media @desktop-xll {
			
			
		}
        &:hover {
			

			-webkit-box-shadow:  -16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(1,179,240,.7);
			-moz-box-shadow:-16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(1,179,240,.7);
			box-shadow: -16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(1,179,240,.7);
		}
		.package-head {
			text-align: center;
			background: #01b3f0;
			padding: 20px 0;
			border-radius: 15px 15px 0px 0px;
			-moz-border-radius: 15px 15px 0px 0px;
			-webkit-border-radius: 15px 15px 0px 0px;
			
			h3 {
				font-size: 33px;
				font-weight: 700;
				color: #fff;
				margin-bottom: 5px;
				@media @mobile {
					font-size: 42px;
				}
				@media @tablet {
					font-size: 22px;
				}
				@media @desktop {
					font-size: 33px;
				}
				@media @desktop-xl {
					font-size: 28px;
				}
				@media @desktop-xl-2 {
					 font-size: 33px;
				}
				@media @desktop-xll {
					
					
				}
			}
			h4 {
				font-size: 27px;
				font-weight: 700;
				color: #fff;
				@media @mobile {
					font-size: 32px;
				}
				@media @tablet {
					font-size: 20px;
				}
				@media @desktop {
					font-size: 27px;
				}
				@media @desktop-xl {
					font-size: 25px;
				}
				@media @desktop-xl-2 {
					 font-size: 27px;
				}
			}
		}
		ul {
			text-align: left;
			li {
				font-size: 18px;
				font-weight: 300;
				color: #000;
				line-height: 45px;
				padding: 0 20px;
				&:hover {
					// background: #e7eff4;
				}
				@media @mobile {
					font-size: 26px;
					line-height: 55px;
				}
				@media @tablet {
					font-size: 15px;
					line-height: 40px;
				}
				@media @desktop {
					font-size: 18px;
					line-height: 45px;
				}
				@media @desktop-xl {
					font-size: 16px;
					line-height: 40px;
				}
				@media @desktop-xl-2 {
					font-size: 18px;
					line-height: 45px;
				}
				img {
					width: 20px;
					height: auto;
					margin-right: 10px;
				}
				&:nth-child(2n) {
					background: #f0f9ff;
				}
			}
		}
		.btn-package {
			width: 306px;
			margin: 0 auto;
			margin-top: 25px;
			-webkit-box-shadow: -2px 23px 28px -12px rgba(244,118,107,0.4);
			-moz-box-shadow: -2px 23px 28px -12px rgba(244,118,107,0.4);
			box-shadow: -2px 23px 28px -12px rgba(244,118,107,0.4);
			@media @mobile {
				width: 340px;
			}
			@media @tablet {
				width: 250px;
			}
			@media @desktop {
				width: 306px;
			}
			@media @desktop-xl {
				
			}
			@media @desktop-xl-2 {
				  
			}
		}
	}
	.item-1 {
		.package-head {
			background-image: -webkit-linear-gradient(right, #01b3f0, #0096ff);
			background-image: -moz-linear-gradient(right, #01b3f0, #0096ff);
			background-image: -o-linear-gradient(right, #01b3f0, #0096ff);
			background-image: -ms-linear-gradient(right, #01b3f0, #0096ff);
			background-image: linear-gradient(to left, #0096ff, #01b3f0);
		}
			@media @mobile {
				display: none;
			}
			@media @tablet {
				
			}
			@media @desktop {

			}
			@media @desktop-xl {
				display: inline-block;
				vertical-align: top;
			}
	}
	.item-2 {
		.package-head {
			background-image: -webkit-linear-gradient(right, #4868db, #4a54b9);
			background-image: -moz-linear-gradient(right, #4868db, #4a54b9);
			background-image: -o-linear-gradient(right, #4868db, #4a54b9);
			background-image: -ms-linear-gradient(right, #4868db, #4a54b9);
			background-image: linear-gradient(to left, #4a54b9, #4868db);
		}
		&:hover {
			

			-webkit-box-shadow:  -16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(72,91,255,.7);
			-moz-box-shadow:-16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(72,91,255,.7);
			box-shadow: -16px 27px 51px rgba(95,82,122,.25), 0 0 35px rgba(72,91,255,.7);

			// box-shadow: inset 0 0 150px rgba(105, 130, 255, 0.2), 0 0 0 0px rgba(255, 255, 255, 1), 0px 0px 25px rgba(105, 130, 255, 1);

		}
		
	}
	.item-3 {
		.package-head {
			background-image: -webkit-linear-gradient(right, #0096ff, #01b3f0);
			background-image: -moz-linear-gradient(right, #0096ff, #01b3f0);
			background-image: -o-linear-gradient(right, #0096ff, #01b3f0);
			background-image: -ms-linear-gradient(right, #0096ff, #01b3f0);
			background-image: linear-gradient(to left, #01b3f0, #0096ff);
		}
	}
}