.footer {
   
    background-image: -webkit-linear-gradient(right, #0f7fde, #1e65ce);
    background-image: -moz-linear-gradient(right, #0f7fde, #1e65ce);
    background-image: -o-linear-gradient(right, #0f7fde, #1e65ce);
    background-image: -ms-linear-gradient(right, #0f7fde, #1e65ce);
    background-image: linear-gradient(to left, #1e65ce, #0f7fde);

    
    @media @mobile {
       text-align: center;
       padding: 35px 0 35px;
    }
    @media @tablet {
        padding: 10px 0;
        text-align: left;
    }
    @media @desktop {
       
    }
    .container {
        @media @mobile {
           
        }
        @media @tablet {

        }
        @media @desktop {   
            
        }
        @media @desktop-xl {
            width: 100%;
            padding: 0 120px;
            
        }
        @media @desktop-xl-2 {
              padding: 0 150px;
        }
        @media @desktop-xll {
            padding: 0 190px;
            
        }
    }
    .left {
        
        @media @mobile {
           
        }
        @media @tablet {
            float: left;
        }
    }
    .logo-2 {
        height: auto;
        @media @mobile {
           width: 250px;
        }
        @media @tablet {
            width: auto;
        }
    }
    
   p {

    
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    
        @media @mobile {
            font-size: 26px;
            margin: 20px 0;
        }
        @media @tablet {
            display: inline-block;
            vertical-align: middle;
            font-size: 16px;
            margin: 0px;
            margin-left: 25px;
        }

   }
   .reserved {
        margin-left: 188px;

        @media @mobile {
            display: none;
        }
        @media @tablet {

        }
        @media @desktop {

        }
        @media @desktop-xl {
            
        }
        @media @desktop-xl-2 {
            display: inline-block;
            vertical-align: middle;
            margin-left: 150px;
        }
        @media @desktop-xll {
            margin-left: 188px;
            
            
        }
   }
   .social {
        
        
        @media @mobile {
            padding-top: 10px;
        }
        @media @tablet {
            float: right;
            padding-top: 5px;
        }
        img {
            width: 35px;
            height: auto;
            margin: 0 7px;
            transition: all 0.2s ease-in-out;
            -webkit-transition: all 0.2s ease-in-out;
            -ms-transition: all 0.2s ease-in-out;
            
            &:hover {
                -webkit-box-shadow:   0 0 10px rgba(83,83,83,.7);
            -moz-box-shadow: 0 0 10px rgba(83,83,83,.7);
            box-shadow:  0 0 10px rgba(83,83,83,.7);
            -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
            transform: scale(1.1);

            }
            @media @mobile {
                width: 55px;
            }
            @media @tablet {
               width: 35px;
            }
        }
   }

    
}