	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(234,246,255,1)), color-stop(41%, rgba(234,246,255,1)), color-stop(41%, rgba(234,246,255,1)), color-stop(41%, rgba(234,246,255,1)), color-stop(72%, rgba(255,255,255,1)), color-stop(100%, rgba(255,255,255,1)));
	background: -webkit-linear-gradient(top, rgba(234,246,255,1) 0%, rgba(234,246,255,1) 41%, rgba(234,246,255,1) 41%, rgba(234,246,255,1) 41%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
