	background: -ms-linear-gradient(top, rgba(234,246,255,1) 0%, rgba(234,246,255,1) 41%, rgba(234,246,255,1) 41%, rgba(234,246,255,1) 41%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
