	    }
	}
	h2 {
		padding-top: 50px;
	}
	&__block {
		text-align: center;
			
		.works-item {
			display: inline-block;
			vertical-align: top;
			width: 320px;
			height: auto;
			margin: 8px;
			position: relative;
			overflow: hidden;


			-webkit-box-shadow: -1px 2px 15px -3px rgba(0,0,0,0.75);
			-moz-box-shadow: -1px 2px 15px -3px rgba(0,0,0,0.75);
			box-shadow: -1px 2px 15px -3px rgba(0,0,0,0.75);

			@media @mobile {
				width: 260px;
			}
			@media @tablet {
				width: 160px;
			}
			@media @desktop {
				width: 222px;
			}
			@media @desktop-xl {
				width: 270px;
			}
			@media @desktop-xl-2 {
				 width: 320px;
			}
			@media @desktop-xll {
				
				
			}

			a {
				img {
					width: 100%;
					height: auto;
				}
				.figcaption {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: rgba( 1, 0, 19, 0);
					transition: all 0.5s ease;
   					-webkit-transition: all 0.5s ease;
       				-ms-transition: all 0.5s ease;
					.vertical-inner {
						top: 38% !important;
						height: auto;
						opacity: 0;
						-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
					    transition: opacity 0.35s, transform 0.35s;
					    // -webkit-transform: translate3d(-360px,0,0);
					    // transform: translate3d(-360px,0,0);
					    transition: all 0.2s ease-in-out;
			            -webkit-transition: all 0.2s ease-in-out;
			            -ms-transition: all 0.2s ease-in-out;
			            -webkit-transform: scale(0.8);
			            -ms-transform: scale(0.8);
			            transform: scale(0.8);
						h3 {
							font-size: 35px;
							font-weight: 700;
							color: #fff;
							margin-bottom: 5px;
													
							
						}
						p {
							font-size: 20px;
							font-weight: 400;
							color: #fff;
						}
						.line {
		
						}
					}
					
				}
			}
			&:hover {
				.figcaption {
					background: rgba( 1, 0, 19, 0.7);
					
					.vertical-inner {	
						opacity: 1;
						-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
					    transition: opacity 0.35s, transform 0.35s;
					     -webkit-transform: scale(1);
			            -ms-transform: scale(1);
			            transform: scale(1);
					    // -webkit-transform: translate3d(0px,0,0);
					    // transform: translate3d(0px,0,0);
					}
				}

			}
			
		}
		
	}
	.btn-work {
		margin: 0 auto;
		margin-top: 50px;
		cursor: pointer;
		position: relative;

		background-image: -webkit-linear-gradient(right, #4570c4, #2fcdf3);
		background-image: -moz-linear-gradient(right, #4570c4, #2fcdf3);
		background-image: -o-linear-gradient(right, #4570c4, #2fcdf3);
		background-image: -ms-linear-gradient(right, #4570c4, #2fcdf3);
		background-image: linear-gradient(to left, #2fcdf3, #4570c4);
		-webkit-box-shadow: -2px 23px 28px -12px rgba(109,202,240,0.8);
		-moz-box-shadow: -2px 23px 28px -12px rgba(109,202,240,0.8);
		box-shadow: -2px 23px 28px -12px rgba(109,202,240,0.8);
		
		&:after {
	        content: "";
	        display: block;
	        position: absolute;
	        top: 0;
	        right: -69px;
	        width: 60px;
	        height: 31px;
	        background: url('@{dir}after.png') no-repeat top center;
	        transition: all 0.5s ease;
    		-webkit-transition: all 0.5s ease;
        	-ms-transition: all 0.5s ease;

	        
    	}
    	&:before {
	        content: "";
	        display: block;
	        position: absolute;
	        top: 51px;
	        left: -37px;
	        width: 60px;
	        height: 31px;
	        background: url('@{dir}before.png') no-repeat top center;
	        transition: all 0.5s ease;
    		-webkit-transition: all 0.5s ease;
        	-ms-transition: all 0.5s ease;
	        
    	}
    	&:hover {
			background-image: linear-gradient(to left, #4570c4, #2fcdf3);
			&:after {
				// right: -67px;
				// transition: all 0.5s ease;
    // 			-webkit-transition: all 0.5s ease;
    //     		-ms-transition: all 0.5s ease;
			}
			&:before {
				// left: -35px;
				// transition: all 0.5s ease;
    // 			-webkit-transition: all 0.5s ease;
    //     		-ms-transition: all 0.5s ease;
			}

			
		}
	}
