	&__item {
		text-align: center;
		br {
			@media @mobile {
				display: none;
			}
			@media @tablet {
				
			}
			@media @desktop {
				display: inline-block;
			}
		}
		img {
			transition: all 0.2s ease-in-out;
            -webkit-transition: all 0.2s ease-in-out;
            -ms-transition: all 0.2s ease-in-out;
			&:hover {
				
            -webkit-transform: scale(1.06);
            -ms-transform: scale(1.06);
            transform: scale(1.06);
			}

			@media @mobile {
				width: 180px;
			}
			@media @tablet {
				width: 140px;
			}
			@media @desktop {
				width: 150px;
			}
			@media @desktop-xl {
				width: 190px;
			}
			@media @desktop-xl-2 {
				width: auto;
				height: auto;
			}
