.remodal-close { 
    top: 12px !important;
    right: 15px !important;
    color: #fff !important;
    @media @mobile {
        width: 28px !important;
        height: 28px !important;
    }
     @media @tablet { 
        width: 10px !important;
        height: 16px !important;
    }
    &:before {
        @media @mobile {
            font-size: 50px !important;
            top: 5px !important;
            left: -5px !important;
        }
         @media @tablet { 
            font-size: 30px !important;
            top: -7px !important;
            left: -12px !important;
        }
    }
}

