.btn {
	// transition: all 0.5s ease;
 //    -webkit-transition: all 0.5s ease;
 //        -ms-transition: all 0.5s ease;
       
}
.btn-orange {
	width: 265px;
	height: 58px;
	-webkit-border-radius: 29px;
	-moz-border-radius: 29px;
	border-radius: 29px;
	// -webkit-box-shadow: 0 2px 8px rgba(51,167,200,.6);
	// -moz-box-shadow: 0 2px 8px rgba(51,167,200,.6);
	// box-shadow: 0 2px 8px rgba(51,167,200,.6);

	
	background-image: -webkit-linear-gradient(right, #ff8a2c, #ff6753);
	background-image: -moz-linear-gradient(right, #ff8a2c, #ff6753);
	background-image: -o-linear-gradient(right, #ff8a2c, #ff6753);
	background-image: -ms-linear-gradient(right, #ff8a2c, #ff6753);
	background-image: linear-gradient(to left, #ff6753, #ff8a2c);

	
	font-size: 22px;
	color: #fff;
	font-weight: 500;
	line-height: 57px;

	&:hover {
		background-image: linear-gradient(to left, #ff8a2c, #ff6753);
	}

	@media @mobile {
		width: 340px;
		height: 75px;
		-webkit-border-radius: 38px;
		-moz-border-radius: 38px;
		border-radius: 38px;
		font-size: 32px;
		line-height: 74px;
    }
    @media @tablet {
        width: 265px;
		height: 58px;
		-webkit-border-radius: 29px;
		-moz-border-radius: 29px;
		border-radius: 29px;
		font-size: 22px;
		line-height: 57px;
    }
}



